<template>
    <div
        v-on="$listeners"
        class="image-loader"
        :style="getStyle()"
    >
        <img
            ref="img"
            :class="imgClass"
            :src="placeHolderSrc"
            :data-src="src"
            :style="getStyle()"
        >
    </div>
</template>

<script>
import 'lazysizes';

export default {
    name: 'ImageLoader',
    props: {
        src: { type: String, required: true },
        isBig: { type: Boolean, default: false },
        CStyle: { type: Object, default: () => ({}) },
    },
    data() {
        return {
            imgClass: 'image lazyload',
        };
    },
    methods: {
        getStyle() {
            return Object.assign({}, this.CStyle);
        },
    },
    computed: {
        placeHolderSrc() {
            const smallSrc = 'data:image/png;base64,';
            const bigSrc = 'data:image/png;base64,';

            if (this.isBig) {
                return bigSrc;
            }

            return smallSrc;
        },
    },
    // 为了修lazysizes与Vue的适配问题：https://github.com/aFarkas/lazysizes/issues/354
    updated() {
        this.$refs.img.className = this.imgClass;
    },
};
</script>

<style scoped>
.image-loader {
    position: relative;
    display: flex;
    justify-content: center;
    align-self: center;
    width: 100%;
    height: 100%;
}

.image {
    width: 100%;
    height: 100%;
}
</style>
